*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 10px;
    font-family: sans-serif;
}
section{
padding: 10rem 0;
}
.heading{
    text-transform: uppercase;
    font-size: 3.5rem;
    letter-spacing: 3px;
    margin-right: 3px;
    margin-bottom: 5rem;
    text-align: center;
    color: yellow;
    position: relative;
}
.heading::after{
    content: '';
    width:10rem;
    height:.8rem;
    background-color: rgb(0, 225, 255);
    position: absolute;
    bottom:-2rem;
    left:50%;
    transform: translateX(-50%);
    border-radius: 2rem;
}
.card-wrapper{

display: flex;
flex-flow: wrap;
justify-content: space-evenly;

}

.card{
    width: 32rem;
    background-color: #ebeef8;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2rem 0;
    box-shadow: .5rem .5rem 3rem rgba(0, 0, 0, 2);
}
.card .card-img{
    width: 100%;
    height: 26rem;
    object-fit: cover;
    clip-path: polygon(0 0, 100% 0, 100% 78%, 0% 100%);
}

.profile-img{
    width: 15rem;
    height: 15rem;
    object-fit: cover;
    border-radius: 50%;
    margin-top: -11rem;
    z-index: 999;
    border:  1rem solid #ebeef8;
}
.card h1{
    font-size: 2.5rem;
    color: #333;
    margin: 1.5rem 0;
}
.job-title{
    color: #777;
    font-size: 1.5rem;
    font-weight: 300;
}
.about{
    font-size: 1.5rem;
    margin: 1.5rem ;
    font-style: italic;
    text-align: center;
    color: #333;
}
